@import url('https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@400&display=swap');
* {
    box-sizing: border-box;
}

.centered {
    margin: auto
}

.Home {
    text-align: center;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    width: 100%;
    min-height: 820px;
    display: flex;
    flex-flow: column;
}

.landingSection {
    width: 100%;
    height: auto;
    min-height: 820px;
    color: #393D3F;
    position: relative;
}


/*Background landing section*/

.upperDivider {
    width: 100%;
    height: 300px;
    background-color: #2A90A9;
    background-image: url(./Shared/Media/memojiiAppleCris.png);
    background-repeat: no-repeat;
    background-size: 200px 200px;
    background-position-y: 143%;
    position: relative;
}

.upperDividerNM {
    width: 100%;
    height: 300px;
    background-color: #F0D85A;
    background-image: url(./Shared/Media/memojiiAppleCris.png);
    background-repeat: no-repeat;
    background-size: 200px 200px;
    background-position-y: 143%;
    position: relative;
}

.wavyDivider {
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    line-height: 0;
    background-color: #2A90A9;
    background-image: url(./Shared/Media/memojiiAppleCris.png);
    background-repeat: no-repeat;
    background-size: 200px 200px;
    background-position-y: 110%;
    background-attachment: scroll;
}

.wavyDividerNM {
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    line-height: 0;
    background-color: #F0D85A;
    background-image: url(./Shared/Media/memojiiAppleCris.png);
    background-repeat: no-repeat;
    background-size: 200px 200px;
    background-position-y: 110%;
    background-attachment: scroll;
}

.wavyDividerNM svg {
    position: relative;
    display: block;
    width: calc(111% + 1.3px);
    height: 30px;
    transform: rotateY(180deg);
}

.wavyDividerNM .shape-fill {
    fill: #393D3F;
}

.wavyDivider svg {
    position: relative;
    display: block;
    width: calc(111% + 1.3px);
    height: 30px;
    transform: rotateY(180deg);
}

.wavyDivider .shape-fill {
    fill: white;
}


/*Background thinking circles*/

.thinkingCircles {
