    width: 200px;
    height: 200px;
    left: 180px;
    bottom: 55px;
    color: white;
    background-color: #393D3F;
    text-align: left;
    padding: 15px;
    font-family: 'Source Code Pro';
    font-size: 13px;
    border-top: 15px solid #4a4949;
    border-radius: 6px;
    border-bottom: 15px solid #0572a0;
    user-select: none;
}

.vsCodeIconCircleOne {
    background-color: rgb(174, 36, 36);
    width: 8px;
    height: 8px;
    border-radius: 100%;
    position: absolute;
    top: -11px;
    left: 10px;
}

.vsCodeIconCircleTwo {
    background-color: rgb(174, 146, 36);
    width: 8px;
    height: 8px;
    border-radius: 100%;
    position: absolute;
    top: -11px;
    left: 25px;
}

.vsCodeIconCircleThree {
    background-color: rgb(32, 150, 8);
    width: 8px;
    height: 8px;
    border-radius: 100%;
    position: absolute;
    top: -11px;
    left: 40px;
}


/*Thinking Circles Night Mode*/

.thinkingCirclesNM {
    position: absolute;
    width: 200px;
    height: 200px;
    left: 180px;
    bottom: 55px;
    background-color: #393D3F;
    text-align: left;
    padding: 15px;
    font-family: 'Source Code Pro';
    font-size: 13px;
    border-top: 15px solid #4a4949;
    border-radius: 6px;
    border-bottom: 15px solid #0572a0;
    user-select: none;
}


/*IMAGE THUMBNAIL*/

.profileImageThumbnail {
    width: 222px;
    height: 222px;
    position: relative;
    margin-top: 50px;
}

.profileImage {
    width: 220px;
    height: 220px;
    border-radius: 100%;
    background-image: url(../src/Shared/Media/memojiiAppleCris.png);
    background-position: center;
    background-repeat: no-repeat;
    /*border: 6px solid #468C9D;*/
    background-size: 230px 240px;
    background-color: #38809E;
    -webkit-box-shadow: 0px 10px 19px 1px #38809E;
    box-shadow: 0px 10px 19px 1px #38809E;
}

.profileImageFlag {
    bottom: -20px;
    right: 0px;
    position: absolute;
    font-size: 60px;
    width: 100px;
    user-select: none;
}


/*TEXT*/

.landingSection>p {
    margin-top: 70px;
    text-align: left;
    width: 93%;
    margin-left: 7%;
}

.landingSection>span {
    font-style: italic;
    font-weight: bold;
    font-size: 48px;
    margin-top: 10px;
    text-align: left;
    display: inline-block;
    width: 100%;
    width: 93%;
    margin-left: 7%;
}

.myProfession {
    text-transform: uppercase;
    font-style: italic;
    color: #38809E;
    font-weight: bolder;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: #38809E;
    font-size: 22px;
    margin-top: 15px;
    line-height: 30px;
    text-align: left;
    width: 93%;
    margin-left: 7%;
}

.myProfessionDetailed {
    font-weight: bold;
    color: #393D3F;
    margin-top: 30px;
    text-align: left;
    width: 93%;
    margin-left: 7%;
}


/*BUTTON*/

.callToAction {
    background-color: #468C9D;
    width: 190px;
    height: 35px;
    color: white;
    margin-top: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
    font-style: italic;
    font-weight: bold;
    cursor: pointer;
    user-select: none;
    -webkit-transition: .1s;
    -o-transition: .1s;
    -moz-transition: .1s;
    transition: .1s;
    margin-left: 7%;
}

.callToAction:hover {
    background-color: white;
    color: #468C9D;
    border: 2px solid #468C9D;
    transform: scale(1.2);
    -webkit-box-shadow: 0px 0px 19px 2px #46a1c7;
    box-shadow: 0px 0px 19px 2px #46a1c7;
    -webkit-transition: .3s;
    -o-transition: .3s;
    -moz-transition: .3s;
    transition: .3s;
}

.lightModeSwitch {
    height: 30px;
    background-color: #393D3F;
    width: 55px;
    border-radius: 20px;
    display: flex;
    align-items: center;
    cursor: pointer;
    position: absolute;
    top: 20px;
    left: 20px;
}


