.moonSun {
    margin-left: 4px;
}

.majorMoonSunCircle {
    opacity: 100;
    fill: #F0D85A;
}

.majorMoonSunCircleNM {
    opacity: 100;
    fill: #393D3F;
}

.minorMoonSunCircle {
    opacity: 100;
    transition: .5s;
}

.minorMoonSunCircleNM {
    opacity: 0;
    transition: .5s;
}

.moon {
    opacity: 0;
    transition: .5s;
}

.moonNM {
    opacity: 100;
    transition: .5s;
    border: 1px solid #F0D85A;
