/*SKILLS SECTION*/

.skillsSection {
    width: 100%;
    height: auto;
    position: relative;
}

.skillsSectionNM {
    width: 100%;
    height: auto;
    position: relative;
    background-color: #393D3F;
}

.sectionTitle {
    position: relative;
    margin: auto;
    width: 85%;
    color: #2A90A9;
    user-select: none;
}

.sectionTitleNM {
    position: relative;
    margin: auto;
    width: 85%;
    color: #F0D85A;
    user-select: none;
}

.sectionTitle>p {
    text-align: left;
    font-size: 36px;
    font-weight: bold;
}

.sectionTitleNM>p {
    text-align: left;
    font-size: 36px;
    font-weight: bold;
}

.webProgramming {
    fill: #2a90a9;
    position: absolute;
    top: 10px;
    left: 60px;
}

.webProgrammingNM {
    fill: #F0D85A;
    position: absolute;
    top: 10px;
