.skillContainer {
    display: grid;
    width: 85%;
    margin: 20px auto;
    grid-template-columns: 1fr 1fr 1fr;
    grid-auto-rows: 1fr;
    gap: 40px;
}

.skillItem {
    border-radius: 6px;
    padding: 10px;
    background-color: #f5f5f5;
    color: #2A90A9;
    font-weight: bold;
    max-width: 170px;
    user-select: none;
}

.skillItem:hover {
    background-color: #2A90A9;
    color: #ffffff;
    cursor: pointer;
    transition: .4s;
}

.skillItemNM {
    border-radius: 6px;
    padding: 10px;
    background-color: #555555;
    color: #F0D85A;
    font-weight: bold;
    max-width: 170px;
    user-select: none;
}

.skillItemNM:hover {
    background-color: #F0D85A;
    color: #555555;
    cursor: pointer;
    transition: .4s;
}

.circuitSvg {
    fill: #2A90A9;
    position: absolute;
    top: 720px;
    right: -95px;
}

.circuitSvgNM {
    fill: #F0D85A;
    position: absolute;
    top: 720px;
    right: -95px;
}

.myWorkSection {
    width: 100%;
    height: auto;
}

.myWorkSectionNM {
    width: 100%;
    height: auto;
    background-color: #393D3F;
}

.upperCaption {
    color: #393D3F;
    opacity: .3;
