    width: 85%;
    margin: 0 auto;
}

.upperCaptionNM {
    color: #ffffff;
    opacity: .4;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 18px;
    width: 85%;
    margin: 0 auto;
}

.workDetail {
    color: #2A90A9;
    font-weight: bold;
    font-size: 30px;
    font-style: italic;
    margin: 0 auto;
    width: 85%;
}

.workDetailNM {
    color: #F0D85A;
    font-weight: bold;
    font-size: 30px;
    font-style: italic;
    margin: 0 auto;
    width: 85%;
}

.linkSvg {
    fill: #2a90A9;
    width: 27px;
    height: 27px;
    opacity: 30%;
}

.linkSvgNM {
    fill: #F0D85A;
    width: 27px;
