}

.workDetailOne {
    text-align: left;
}

.workDetailTwo:hover {
    text-decoration: underline;
    cursor: pointer;
    user-select: none;
}

.upperCaptionOne {
    text-align: left;
}

.upperCaptionTwo {
    margin-top: 20px;
    text-align: center;
}

.upperCaptionThree {
    margin-top: 40px;
    text-align: left;
}

.workSkillsGrid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(6, 1fr);
    width: 85%;
    margin: 0 auto;
    margin-top: 40px;
    row-gap: 20px;
}

.workSkillsGrid>.skillItem {
    background-color: #2A90A9;
    color: white;
    cursor: auto;
}

.workSkillsGrid>.skillItemNM {
    background-color: #F0D85A;
    color: #393D3F;
    cursor: auto;
}

.workSkillsGrid>div:nth-child(1) {
    grid-column: 1/2;
    grid-row: 1 / 2;
}

.workSkillsGrid>div:nth-child(2) {
    grid-column: 2 / 3;
    grid-row: 2 / 3;
}

.workSkillsGrid>div:nth-child(3) {
    grid-column: 3/4;
    grid-row: 3/4;
}

.workSkillsGrid>div:nth-child(4) {
    grid-column: 1/2;
    grid-row: 4/5;
}

.workSkillsGrid>div:nth-child(5) {
    grid-column: 2/3;
    grid-row: 5/6;
}

.workSkillsGrid>div:nth-child(6) {
    grid-column: 3/4;
    grid-row: 6/7;
}